.list {
  list-style: none;
  padding: 0;
}
.item {
  display: flex;
  width: 250px;
  height: 60px;
  align-items: center;
  background-color: rgb(209, 209, 209);
  margin-bottom: 4px;
  padding: 0 6px;
  justify-content: space-between;
}

.span {
  display: block;
  color: dimgrey;
}
